'use client';

import { useState, useEffect } from 'react';
import { graphql, CREATE_APPLICATION, GET_APPLICATIONS, START_CRAWL, GET_CRAWL_JOBS, Application, CrawlJob } from '@/lib/api';

import { Button } from '@/components/ui/button';
import Link from 'next/link';

export default function CrawlerPage() {
    const [applications, setApplications] = useState<Application[]>([]);
    const [crawlJobs, setCrawlJobs] = useState<CrawlJob[]>([]);
    const [loading, setLoading] = useState(false);
    const [showNewAppForm, setShowNewAppForm] = useState(false);

    // Form states
    const [newApp, setNewApp] = useState({
        name: '',
        urlDocBase: '',
        description: '',
    });
    const [selectedApp, setSelectedApp] = useState<string>('');

    useEffect(() => {
        loadApplications();
        loadCrawlJobs();
    }, []);

    const loadApplications = async () => {
        try {
            const data = await graphql.request<{ applications: Application[] }>(GET_APPLICATIONS);
            setApplications(data.applications);
        } catch (error) {
            console.error('Failed to load applications:', error);
        }
    };

    const loadCrawlJobs = async () => {
        try {
            const data = await graphql.request<{ crawlJobs: CrawlJob[] }>(GET_CRAWL_JOBS, {
                limit: 20,
            });
            setCrawlJobs(data.crawlJobs);
        } catch (error) {
            console.error('Failed to load crawl jobs:', error);
        }
    };

    const handleCreateApp = async (e: React.FormEvent) => {
        e.preventDefault();
        setLoading(true);
        try {
            await graphql.request(CREATE_APPLICATION, {
                input: newApp,
            });
            setNewApp({ name: '', urlDocBase: '', description: '' });
            setShowNewAppForm(false);
            loadApplications();
        } catch (error) {
            console.error('Failed to create application:', error);
            alert('Failed to create application');
        } finally {
            setLoading(false);
        }
    };

    const handleStartCrawl = async (appId: string) => {
        setLoading(true);
        try {
            const app = applications.find(a => a.id === appId);
            if (!app) return;

            await graphql.request(START_CRAWL, {
                input: {
                    appId,
                    url: app.urlDocBase,
                    maxDepth: 3,
                    maxPages: 100,
                },
            });
            alert('Crawl started successfully!');
            setTimeout(loadCrawlJobs, 2000);
        } catch (error) {
            console.error('Failed to start crawl:', error);
            alert('Failed to start crawl');
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="container">
            <div className="flex flex-col gap-5 lg:gap-7.5">
                {/* Page Header */}
                <div className="flex flex-wrap items-center gap-5 justify-between">
                    <div className="flex flex-col justify-center gap-2">
                        <h1 className="text-xl font-semibold leading-none text-gray-900">
                            Crawler Management
                        </h1>
                        <div className="flex items-center flex-wrap gap-1.5 font-medium">
                            <span className="text-md text-gray-600">
                                Manage documentation sites and crawl jobs
                            </span>
                        </div>
                    </div>
                    <div className="flex items-center gap-2.5">
                        <Link href="/crawler/users">
                            <Button variant="outline" size="sm">
                                Manage Users
                            </Button>
                        </Link>
                        <Button
                            variant="primary"
                            size="sm"
                            onClick={() => setShowNewAppForm(!showNewAppForm)}
                        >
                            + New Application
                        </Button>
                    </div>
                </div>

                {/* New Application Form */}
                {showNewAppForm && (
                    <div className="card">
                        <div className="card-header">
                            <h3 className="card-title">Add New Application</h3>
                        </div>
                        <div className="card-body">
                            <form onSubmit={handleCreateApp} className="flex flex-col gap-5">
                                <div className="flex flex-col gap-1">
                                    <label className="form-label">Name</label>
                                    <input
                                        type="text"
                                        className="input"
                                        value={newApp.name}
                                        onChange={(e) => setNewApp({ ...newApp, name: e.target.value })}
                                        required
                                        placeholder="e.g., ClientSync Docs"
                                    />
                                </div>
                                <div className="flex flex-col gap-1">
                                    <label className="form-label">Documentation URL</label>
                                    <input
                                        type="url"
                                        className="input"
                                        value={newApp.urlDocBase}
                                        onChange={(e) => setNewApp({ ...newApp, urlDocBase: e.target.value })}
                                        required
                                        placeholder="https://docs.example.com"
                                    />
                                </div>
                                <div className="flex flex-col gap-1">
                                    <label className="form-label">Description</label>
                                    <textarea
                                        className="input"
                                        value={newApp.description}
                                        onChange={(e) => setNewApp({ ...newApp, description: e.target.value })}
                                        placeholder="Optional description"
                                        rows={3}
                                    />
                                </div>
                                <div className="flex gap-2.5">
                                    <Button type="submit" variant="primary" size="sm" disabled={loading}>
                                        {loading ? 'Creating...' : 'Create Application'}
                                    </Button>
                                    <Button
                                        type="button"
                                        variant="secondary"
                                        size="sm"
                                        onClick={() => setShowNewAppForm(false)}
                                    >
                                        Cancel
                                    </Button>
                                </div>
                            </form>
                        </div>
                    </div>
                )}

                {/* Applications Grid */}
                <div className="grid grid-cols-1 lg:grid-cols-2 gap-5 lg:gap-7.5">
                    {applications.map((app) => (
                        <div key={app.id} className="card">
                            <div className="card-header">
                                <h3 className="card-title">{app.name}</h3>
                            </div>
                            <div className="card-body">
                                <div className="flex flex-col gap-3">
                                    <div>
                                        <span className="text-sm text-gray-600">URL:</span>
                                        <a
                                            href={app.urlDocBase}
                                            target="_blank"
                                            rel="noopener noreferrer"
                                            className="text-sm text-primary ml-2"
                                        >
                                            {app.urlDocBase}
                                        </a>
                                    </div>
                                    {app.description && (
                                        <div>
                                            <span className="text-sm text-gray-600">Description:</span>
                                            <p className="text-sm ml-2">{app.description}</p>
                                        </div>
                                    )}
                                    <Button
                                        onClick={() => handleStartCrawl(app.id)}
                                        variant="primary"
                                        size="sm"
                                        disabled={loading}
                                    >
                                        Start Crawl
                                    </Button>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>

                {/* Recent Crawl Jobs */}
                <div className="card">
                    <div className="card-header">
                        <h3 className="card-title">Recent Crawl Jobs</h3>
                    </div>
                    <div className="card-body">
                        <div className="table-responsive">
                            <table className="table table-auto">
                                <thead>
                                    <tr>
                                        <th className="min-w-[250px]">Application</th>
                                        <th className="min-w-[150px]">Status</th>
                                        <th className="min-w-[150px]">Pages</th>
                                        <th className="min-w-[200px]">Started</th>
                                        <th className="min-w-[150px]">Duration</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {crawlJobs.map((job) => {
                                        const app = applications.find(a => a.id === job.appId);
                                        const duration = job.endTime
                                            ? Math.round((new Date(job.endTime).getTime() - new Date(job.startTime).getTime()) / 1000)
                                            : '-';

                                        return (
                                            <tr key={job.id}>
                                                <td>{app?.name || job.appId}</td>
                                                <td>
                                                    <span className={`badge badge-sm ${job.status === 'completed' ? 'badge-success' :
                                                        job.status === 'running' ? 'badge-primary' :
                                                            job.status === 'failed' ? 'badge-danger' :
                                                                'badge-secondary'
                                                        }`}>
                                                        {job.status}
                                                    </span>
                                                </td>
                                                <td>{job.pagesProcessed || 0}</td>
                                                <td className="text-sm">{new Date(job.startTime).toLocaleString()}</td>
                                                <td>{typeof duration === 'number' ? `${duration}s` : duration}</td>
                                            </tr>
                                        );
                                    })}
                                </tbody>
                            </table>
                            {crawlJobs.length === 0 && (
                                <div className="text-center py-10 text-gray-500">
                                    No crawl jobs yet
                                </div>
                            )}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
